html {
    scroll-behavior: smooth;
}
#project-container {
    display: flex;
    display: grid;
    grid-gap: 20px;
    grid-template-columns: 1fr 1fr;
    width: 10%;
    justify-items: center;
    align-content: center;
}
.projects {
    display: flex;
    justify-content: center;
    margin-top: 10%;
    margin-bottom: 10%;
}
body {
    background-color: #DADDE2;
}
